import { NextRequest, NextResponse } from 'next/server'
import { getUserFromRequest } from '@/lib/auth-helper'
import { prisma } from '@/lib/db'

// Force dynamic to prevent caching issues
export const dynamic = 'force-dynamic'
export const runtime = 'nodejs'

// Maximum file size: 2MB (stored as base64 in database)
const MAX_FILE_SIZE = 2 * 1024 * 1024

// POST /api/user/avatar - Upload avatar (base64 stored in database)
export async function POST(request: NextRequest) {
  try {
    const user = await getUserFromRequest(request)
    
    if (!user?.id) {
      return NextResponse.json({ error: 'Unauthorized' }, { status: 401 })
    }

    if (!prisma) {
      return NextResponse.json({ error: 'Database not available' }, { status: 503 })
    }

    const formData = await request.formData()
    const file = formData.get('avatar') as File | null

    if (!file) {
      return NextResponse.json({ error: 'No file uploaded' }, { status: 400 })
    }

    // Validate file type
    const allowedTypes = ['image/jpeg', 'image/png', 'image/gif', 'image/webp']
    if (!allowedTypes.includes(file.type)) {
      return NextResponse.json({ 
        error: 'Invalid file type. Only JPEG, PNG, GIF, and WebP are allowed.' 
      }, { status: 400 })
    }

    // Validate file size
    if (file.size > MAX_FILE_SIZE) {
      return NextResponse.json({ 
        error: 'File too large. Maximum size is 2MB.' 
      }, { status: 400 })
    }

    // Convert file to base64 data URL
    const bytes = await file.arrayBuffer()
    const buffer = Buffer.from(bytes)
    const base64 = buffer.toString('base64')
    const dataUrl = `data:${file.type};base64,${base64}`

    // Update user avatar
    await prisma.user.update({
      where: { id: user.id },
      data: {
        avatar: dataUrl,
        updatedAt: new Date(),
      }
    })

    console.log(`[Avatar Upload] User ${user.email} uploaded new avatar`)

    return NextResponse.json({
      success: true,
      avatarUrl: dataUrl,
      message: 'Avatar uploaded successfully'
    })
  } catch (error: any) {
    console.error('[Avatar API] Upload error:', error?.message || error)
    return NextResponse.json({ error: 'Failed to upload avatar' }, { status: 500 })
  }
}

// DELETE /api/user/avatar - Remove avatar
export async function DELETE(request: NextRequest) {
  try {
    const user = await getUserFromRequest(request)
    
    if (!user?.id) {
      return NextResponse.json({ error: 'Unauthorized' }, { status: 401 })
    }

    if (!prisma) {
      return NextResponse.json({ error: 'Database not available' }, { status: 503 })
    }

    // Clear avatar
    await prisma.user.update({
      where: { id: user.id },
      data: {
        avatar: null,
        updatedAt: new Date(),
      }
    })

    console.log(`[Avatar Remove] User ${user.email} removed avatar`)

    return NextResponse.json({
      success: true,
      message: 'Avatar removed successfully'
    })
  } catch (error: any) {
    console.error('[Avatar API] Remove error:', error?.message || error)
    return NextResponse.json({ error: 'Failed to remove avatar' }, { status: 500 })
  }
}
