/**
 * Auth Helper - Decodes JWT tokens from our custom login endpoint
 * Used by API routes to get the current user from the request
 */

import { NextRequest } from 'next/server'

export interface TokenPayload {
  id: string
  email: string
  name: string
  role: string
  isMasterAdmin: boolean
  status: string
  agencyId: string | null
  agencyName: string
  sub: string
  iat: number
  exp: number
}

/**
 * Decode a JWT token without verification
 * (Verification is done by checking expiration only - signature verification
 * should be done if needed for sensitive operations)
 */
export function decodeJWT(token: string): TokenPayload | null {
  try {
    const parts = token.split('.')
    if (parts.length !== 3) return null
    
    const payload = parts[1]
    const decoded = JSON.parse(Buffer.from(payload, 'base64url').toString('utf-8'))
    
    // Check expiration
    if (decoded.exp && decoded.exp < Math.floor(Date.now() / 1000)) {
      return null // Token expired
    }
    
    return decoded as TokenPayload
  } catch {
    return null
  }
}

/**
 * Get the current user from the request by reading and decoding the JWT cookie
 */
export async function getUserFromRequest(req: NextRequest): Promise<TokenPayload | null> {
  try {
    // Check both production and dev cookie names
    const prodCookie = req.cookies.get('__Secure-authjs.session-token')?.value
    const devCookie = req.cookies.get('authjs.session-token')?.value
    const tokenValue = prodCookie || devCookie

    if (!tokenValue) {
      return null
    }

    return decodeJWT(tokenValue)
  } catch (error) {
    console.error('[Auth Helper] Error getting user from request:', error)
    return null
  }
}
